import { Carousel } from "flowbite-react"
import piC1 from './Bannersvg/1.png'
import piC2 from './Bannersvg/2.png'
import piC3 from './Bannersvg/3.png'


function Carsel(){






return(


<>
<div className="h-56 sm:h-64 xl:h-80 2xl:h-96">
  <Carousel>
    <img
      src={piC1}
      alt="..."
    />
    <img
      src={piC2}
      alt="..."
    />
    <img
      src={piC3}
      alt="..."
    />
  </Carousel>
</div>

</>


)




}
export default Carsel;