import { Card } from "flowbite-react";
import piC1 from "./Bannersvg/4.png";
import piC2 from "./Bannersvg/5.png";
import piC3 from "./Bannersvg/6.png";

function Cardi() {
  return (
    <div className="flex justify-center bg-sky-950">
      <div className="max-w-xs">
      <Card
        imgAlt="Meaningful alt text for an image that is not purely decorative"
        imgSrc={piC1}
      >
        <h5 className="text-2xl font-bold tracking-tight text-gray-900 dark:text-white">
          Javascript
        </h5>
        <p className="font-normal max-w-prose text-gray-700 dark:text-gray-400 object-contain">
          JavaScript (/ˈdʒɑːvəskrɪpt/), often abbreviated as JS, is a
          programming language that is one of the core technologies of the World
          Wide Web, alongside HTML and CSS. As of 2022, 98% of websites use
          JavaScript on the client side for webpage behavior, often
          incorporating third-party libraries. All major web browsers have a
          dedicated JavaScript engine to execute the code on users' device.
        </p>
      </Card>
      </div>
      <div className="max-w-xs ml-20">
      <Card
        imgAlt="Meaningful alt text for an image that is not purely decorative"
        imgSrc={piC2}
      >
        <h5 className="text-2xl font-bold tracking-tight text-gray-900 dark:text-white">
          Tailwind
        </h5>
        <p className="font-normal max-w-prose text-gray-700 dark:text-gray-400">
          Tailwind CSS is an open source CSS framework. The main feature of this
          library is that, unlike other CSS frameworks like Bootstrap, it does
          not provide a series of predefined classes for elements such as
          buttons or tables. Instead, it creates a list of "utility" CSS classes
          that can be used to style each element by mixing and matching
        </p>
      </Card>
      </div>
      <div className="max-w-xs ml-20">
      <Card
        imgAlt="Meaningful alt text for an image that is not purely decorative"
        imgSrc={piC3}
      >
        <h5 className="text-2xl font-bold tracking-tight text-gray-900 dark:text-white">
          Flowbite
        </h5>
        <p className="font-normal max-w-prose text-gray-700 dark:text-gray-400 ">
          Flowbite is an ecosystem built on top of Tailwind CSS including a
          component library, block sections, a Figma design system and other
          resources. Resources.
        </p>
      </Card>
    </div>
    </div>
  );
}

export default Cardi;
