import { Accordion } from "flowbite-react"


function Accord(){

return(

<>
<Accordion>
  
  <Accordion.Panel>
    <Accordion.Title className="bg-sky-950 text-white">
      Javascript
    </Accordion.Title>
    <Accordion.Content className="bg-sky-400 text-white">
      <p className="mb-2  text-white">
      JavaScript, often abbreviated as JS, is a programming language that is one of the core technologies of the World Wide Web, alongside HTML and CSS. As of 2022, 98% of websites use JavaScript on the client side for webpage behavior, often incorporating third-party libraries.
      </p>
    </Accordion.Content>
  </Accordion.Panel>
  <Accordion.Panel>
    <Accordion.Title className="bg-sky-950 text-white">
      HTML
    </Accordion.Title>
    <Accordion.Content className="bg-sky-400 text-white">
      <p className="mb-2  text-white">
      The HyperText Markup Language or HTML is the standard markup language for documents designed to be displayed in a web browser. It is often assisted by technologies such as Cascading Style Sheets and scripting languages such as JavaScript.
      </p>
    </Accordion.Content>
  </Accordion.Panel>
  <Accordion.Panel>
    <Accordion.Title className="bg-sky-950 text-white">
      CSS Style Design
    </Accordion.Title>
    <Accordion.Content className="bg-sky-400">
      <p className="mb-2  text-white">
      Cascading Style Sheets is a style sheet language used for describing the presentation of a document written in a markup language such as HTML or XML. CSS is a cornerstone technology of the World Wide Web, alongside HTML and JavaScript.
      </p>
    </Accordion.Content>
  </Accordion.Panel>
</Accordion>

</>


)




}

export default Accord;